@import url(https://fonts.googleapis.com/css2?family=Noto+Serif&family=Noto+Serif+JP:wght@300;400&display=swap);
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Noto Serif JP', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.header {
    height: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.header p {
    font-size: 36px;
    margin: 0
}
.body {
    height: 80vh;

    margin: 0 0 20px 0;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.search {
    height: 10%;
    width: 80%;
    display: flex;
    justify-content: center;
    align-items: flex-end;

    border-bottom: 2px solid #E3E3E3;
}
.searchForm {
    height: 70%;
    width: 100%;

    display: flex;
    justify-content: space-between;
    align-items: center;
}
.formInput {
    height: 100%;
    width: 80%;

    font-family: 'Noto Serif JP', serif;
    font-size: 16px;
    padding-left: 20px;

    border: none
}
::-webkit-input-placeholder {
    color: grey;
}
:-ms-input-placeholder {
    color: grey;
}
::-ms-input-placeholder {
    color: grey;
}
::placeholder {
    color: grey;
}

.formButton {
    background: none;
    border: none;

    margin-right: 20px;
}
.result {
    height: 90%;
    width: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
}

.result-card {
    height: 90%;
    width: 30%;
    min-width: 300px;

    border-radius: 10px;
    /* background: #4B6EA5; */
}

.result-card-header {
    height: 30%;
    width: 100%;

    display: flex;
    justify-content: space-between;
    align-items: center;
}

.result-card-header-1 {
    margin-left: 20px;
}
.result-card-header-2 {
    margin-right: 20px;
}

.title {
    font-size: 48px;
    font-weight: bolder;
    color: white;

    margin: 0;
}
.temp {
    font-size: 24px;
    color: white;

    margin: 0
}
.city {
    font-size: 18px;
    color: white;

    margin: 0
}

.result-card-body {
    height: 70%;
    width: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
}
.footer {
    height: 10vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.footer p {
    margin-top: 0;
}
.badge {
    height: 30px;
    width: 30px;

    margin: 0 10px;
}
